@import-normalize;
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap');
@import "buttons.css";
@import "squares.css";
@import "board.css";
@import "board-info.css";
@import "FEN-generator.css";
@import "piece.css";
@import "FEN-parser.css";
@import "notification-modal.css";
@import "board-options.css";
*,
*::after,
*::before {
  box-sizing: border-box;
}
:root{
  --piece-size:50px;
  --board-size: calc(var(--piece-size)*9)
}
body {
  margin: 0;
  padding: 0;
  background: white;
  color: white;
  margin: 0 auto;
  max-width: 800px;
}
*{
  font-family: Raleway, sans-serif;
}
h1 {
  color: black;
  border-radius: 5px;
  padding: 10px 40px;
  font-weight: bold;
  font-size: 35px;
  text-align: center;
}
.red {
  background-color: hsl(9.18, 95.51%, 34.9%);
}
.black {
  background-color: hsl(46.67, 13.85%, 12.75%);
}
.app__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 30px;
}

.app__container {
  display: grid;
  grid-template-columns: 450px 1fr;
  grid-template-rows: 500px;
  gap: 50px;
}

@media (max-width: 900px) {
  .app__header {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .app__container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 0px;
  }
  .board-option__container{
    width: var(--board-size);
  }
  .board{
    width: 540px;
  }
  .board-info {
    width: var(--board-size);
    margin: 10px auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  :root{
    --piece-size:60px;
  }
  .FEN-generator__container{
    margin-top: 20px;
  }
}
@media (max-width: 540px){
  :root{
    --piece-size:calc(100vw/9 )
  }
  .board{
    padding: 0;
    margin: 0;
    width: 100%;
  }
  .board-info{
    width: 100%;
  }
  .board-option__container{
    width: 100%;
  }
}